/**
 * @jest-environment jsdom
 */
import { renderHook, act } from '@testing-library/react';
import AsyncStorage from '@react-native-async-storage/async-storage';
import useActivityMetrics from '../../src/hooks/useActivityMetrics';

// ðŸ§ª Mock AsyncStorage
jest.mock('@react-native-async-storage/async-storage', () => ({
  setItem: jest.fn(() => Promise.resolve()),
  getItem: jest.fn(() => Promise.resolve(null)),
  removeItem: jest.fn(() => Promise.resolve()),
}));

describe('useActivityMetrics', () => {
  const MOCK_DATE = new Date('2025-01-01T12:00:00.000Z');
  const MOCK_TODAY = MOCK_DATE.toDateString();
  const ONE_DAY = 24 * 60 * 60 * 1000;
  const TWO_DAYS = 2 * ONE_DAY;

  beforeEach(() => {
    jest.useFakeTimers();
    jest.setSystemTime(MOCK_DATE);
    jest.clearAllMocks();
  });

  afterEach(() => {
    jest.runOnlyPendingTimers();
    jest.useRealTimers();
    jest.clearAllTimers();
  });

  it('loads metrics and initializes default state', async () => {
    AsyncStorage.getItem.mockResolvedValueOnce(JSON.stringify({ totalMinutes: 100, lastActivityDate: MOCK_TODAY }));

    const { result, waitForNextUpdate } = renderHook(() => useActivityMetrics());
    expect(result.current.isLoading).toBe(true);

    await waitForNextUpdate();

    expect(result.current.isLoading).toBe(false);
    expect(result.current.metrics).toEqual({ totalMinutes: 100, lastActivityDate: MOCK_TODAY });
    expect(result.current.todayMinutes).toBe(0); // Should be 0 initially for a new day
    expect(result.current.formattedTime).toBe('0min');
    expect(result.current.currentStreak).toBe(0);
    expect(AsyncStorage.getItem).toHaveBeenCalledWith('activity_metrics');
    expect(AsyncStorage.getItem).toHaveBeenCalledWith('today_minutes');
    expect(AsyncStorage.getItem).toHaveBeenCalledWith('last_time_date');
    expect(AsyncStorage.getItem).toHaveBeenCalledWith('current_streak');
    expect(AsyncStorage.getItem).toHaveBeenCalledWith('last_activity_date');
  });

  it('loads existing today_minutes and last_time_date', async () => {
    AsyncStorage.getItem
      .mockResolvedValueOnce(null) // activity_metrics
      .mockResolvedValueOnce('15') // today_minutes
      .mockResolvedValueOnce(MOCK_TODAY); // last_time_date

    const { result, waitForNextUpdate } = renderHook(() => useActivityMetrics());
    await waitForNextUpdate();

    expect(result.current.todayMinutes).toBe(15);
    expect(result.current.formattedTime).toBe('15min');
  });

  it('resets today_minutes if last_time_date is not today', async () => {
    AsyncStorage.getItem
      .mockResolvedValueOnce(null) // activity_metrics
      .mockResolvedValueOnce('20') // today_minutes
      .mockResolvedValueOnce(new Date(MOCK_DATE.getTime() - ONE_DAY).toDateString()); // last_time_date (yesterday)

    const { result, waitForNextUpdate } = renderHook(() => useActivityMetrics());
    await waitForNextUpdate();

    expect(result.current.todayMinutes).toBe(0);
    expect(result.current.formattedTime).toBe('0min');
    expect(AsyncStorage.setItem).toHaveBeenCalledWith('today_minutes', '0');
    expect(AsyncStorage.setItem).toHaveBeenCalledWith('last_time_date', MOCK_TODAY);
  });

  it('tracks session duration and stores minutes', async () => {
    const { result, waitForNextUpdate } = renderHook(() => useActivityMetrics());
    await waitForNextUpdate();

    act(() => result.current.startSession());
    act(() => jest.advanceTimersByTime(2 * 60 * 1000)); // 2min

    await act(async () => result.current.endSession());

    expect(result.current.todayMinutes).toBe(2);
    expect(result.current.formattedTime).toBe('2min');
    expect(AsyncStorage.setItem).toHaveBeenCalledWith('today_minutes', '2');
    expect(AsyncStorage.setItem).toHaveBeenCalledWith('last_time_date', MOCK_TODAY);
    expect(AsyncStorage.setItem).toHaveBeenCalledWith('activity_metrics', JSON.stringify({
      totalMinutes: 2,
      lastActivityDate: MOCK_TODAY,
    }));
  });

  it('updates totalMinutes in metrics when session ends', async () => {
    AsyncStorage.getItem.mockResolvedValueOnce(JSON.stringify({ totalMinutes: 10 }));

    const { result, waitForNextUpdate } = renderHook(() => useActivityMetrics());
    await waitForNextUpdate();

    act(() => result.current.startSession());
    act(() => jest.advanceTimersByTime(5 * 60 * 1000)); // 5min
    await act(async () => result.current.endSession());

    expect(result.current.metrics.totalMinutes).toBe(15);
    expect(AsyncStorage.setItem).toHaveBeenCalledWith('activity_metrics', JSON.stringify({
      totalMinutes: 15,
      lastActivityDate: MOCK_TODAY,
    }));
  });

  it('increments streak if last activity was yesterday', async () => {
    AsyncStorage.getItem
      .mockResolvedValueOnce(null)
      .mockResolvedValueOnce(JSON.stringify({ currentStreak: 1 }))
      .mockResolvedValueOnce(new Date(MOCK_DATE.getTime() - ONE_DAY).toDateString());

    const { result, waitForNextUpdate } = renderHook(() => useActivityMetrics());
    await waitForNextUpdate();

    act(() => result.current.updateStreak());
    await waitForNextUpdate();

    expect(result.current.currentStreak).toBe(2);
    expect(AsyncStorage.setItem).toHaveBeenCalledWith('current_streak', '2');
    expect(AsyncStorage.setItem).toHaveBeenCalledWith('last_activity_date', MOCK_TODAY);
  });

  it('resets streak if last activity was more than a day ago', async () => {
    AsyncStorage.getItem
      .mockResolvedValueOnce(null)
      .mockResolvedValueOnce(JSON.stringify({ currentStreak: 5 }))
      .mockResolvedValueOnce(new Date(MOCK_DATE.getTime() - TWO_DAYS).toDateString());

    const { result, waitForNextUpdate } = renderHook(() => useActivityMetrics());
    await waitForNextUpdate();

    act(() => result.current.updateStreak());
    await waitForNextUpdate();

    expect(result.current.currentStreak).toBe(1);
    expect(AsyncStorage.setItem).toHaveBeenCalledWith('current_streak', '1');
    expect(AsyncStorage.setItem).toHaveBeenCalledWith('last_activity_date', MOCK_TODAY);
  });

  it('does not update streak if already updated today', async () => {
    AsyncStorage.getItem
      .mockResolvedValueOnce(null)
      .mockResolvedValueOnce(JSON.stringify({ currentStreak: 3 }))
      .mockResolvedValueOnce(MOCK_TODAY);

    const { result, waitForNextUpdate } = renderHook(() => useActivityMetrics());
    await waitForNextUpdate();

    act(() => result.current.updateStreak());
    await waitForNextUpdate();

    expect(result.current.currentStreak).toBe(3);
    expect(AsyncStorage.setItem).not.toHaveBeenCalledWith('current_streak', expect.any(String));
  });

  it('returns correct streakTrend according to streak value', async () => {
    const { result, waitForNextUpdate, rerender } = renderHook(() => useActivityMetrics());
    await waitForNextUpdate();

    // Set and rerender for various streaks
    act(() => { result.current.currentStreak = 1; });
    rerender();
    expect(result.current.streakTrend).toBe('ðŸ”¥ Continue!');

    act(() => { result.current.currentStreak = 3; });
    rerender();
    expect(result.current.streakTrend).toBe('ðŸ’ª En forme!');

    act(() => { result.current.currentStreak = 7; });
    rerender();
    expect(result.current.streakTrend).toBe('ðŸ† Incroyable!');

    act(() => { result.current.currentStreak = 0; });
    rerender();
    expect(result.current.streakTrend).toBeNull();
  });

  it('returns formattedTime correctly for long durations', async () => {
    const { result, waitForNextUpdate } = renderHook(() => useActivityMetrics());
    await waitForNextUpdate();

    act(() => result.current.startSession());
    act(() => jest.advanceTimersByTime(30 * 60 * 1000));
    await act(async () => result.current.endSession());
    expect(result.current.formattedTime).toBe('30min');

    act(() => result.current.startSession());
    act(() => jest.advanceTimersByTime(60 * 60 * 1000));
    await act(async () => result.current.endSession());
    expect(result.current.formattedTime).toBe('1h');

    act(() => result.current.startSession());
    act(() => jest.advanceTimersByTime(75 * 60 * 1000));
    await act(async () => result.current.endSession());
    expect(result.current.formattedTime).toBe('2h15min');
  });
});